
.ui-popover{background-color:white;outline:none
}
.ui-popover.is-raised{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)
}
.ui-popover .ui-menu{border:none
}
.ui-popover__focus-redirector{opacity:0;position:absolute
}
.drop-element{display:none;max-height:100%;max-width:100%;opacity:0;position:absolute;transition:opacity 0.2s ease;z-index:60
}
.drop-element,.drop-element:after,.drop-element:before,.drop-element *,.drop-element *:after,.drop-element *:before{box-sizing:border-box
}
.drop-element.drop-open{display:block
}
.drop-element.drop-after-open{opacity:1
}



