
.boxed.phpdoc-method{border:1px solid #c5d0dc
}
.phpdoc-method .method-details{padding:10px 20px 10px 20px
}
.phpdoc-method .method-block-title{font-size:0.7rem;border-bottom:1px solid #e2e2e2;padding-left:5px;margin:10px -20px 10px 0px;text-transform:uppercase;color:rgba(0,0,0,0.5);position:relative;left:-20px
}
.phpdoc-method .method-block-title:first-child{margin-top:0
}
.phpdoc-method .method-block{padding-left:5px
}
.phpdoc-method .method-arguments{list-style-type:none;margin-left:0
}
.phpdoc-method .method-arguments:first-child{counter-reset:customlistcounter
}
.phpdoc-method .method-arguments>li{counter-increment:customlistcounter
}
.phpdoc-method .method-arguments>li:before{content:counter(customlistcounter) " ";position:relative;left:-15px;font-size:0.7rem;color:lightgrey;top:9px;float:left
}
.phpdoc-method .method-argument{border-top:1px dashed #f1f1f1;border-bottom:1px dashed #f1f1f1;border-left:2px solid #e6e4e4;padding:3px 0 3px 5px
}
.phpdoc-method .method-argument+.method-block{border-left:2px solid #e6e4e4;margin-bottom:10px
}
.phpdoc-method .method-argument+.method-block p{margin:0;font-size:0.9rem;padding-top:2px
}
.phpdoc-method .phpdoc-method-signature+.method-details{border-top:1px solid #c5d0dc
}



